import './App.css';
import React from 'react';
import Robot from './robot';
import './robot.css';
import Navbars from './navbar.js';
import Animated from './Animated';
import SearchBox from './SearchBox';
import './additional.css';
import Buutton from './Button';
import ParticlesBg from 'particles-bg';

class App extends React.Component {

  constructor(){
    super();
    this.state = {
      searchfield: '',
      searchfield2: ''
    }
  }

  onSearchChange = (event) => {
    console.log(this.state.searchfield);
    this.setState({ searchfield2: event.target.value });  
  }

  onEnter = (event1) => {
    if (event1.key === 'Enter'){
      console.log(this.state.searchfield);
      this.setState({ searchfield: event1.target.value });
      // event1.target.reset(); 
    }
  }

  onclickchange = (event) => {
     this.setState({ searchfield: this.state.searchfield2 })
     console.log(this.state.searchfield);
  }

  render(){
    const {searchfield, searchfield2} = this.state;
    console.log(searchfield, searchfield2);
    return(
          <>
            <div className='navig'>
              <Navbars></Navbars>
            </div>

            <div className="App">  
              <header className="App-header">
                <Animated></Animated>
                <Robot c={searchfield.toLowerCase()}></Robot>

                <SearchBox onEnterSearch={this.onEnter} onsearch = {this.onSearchChange}/>
                <Buutton onclicking = {this.onclickchange} />

                <h1 className='h'>Help table</h1>
                <h2 className='h'>Type in the command input from the table below and press 'Enter' to see the robot dance OR hover the mouse over the robot to see it move</h2>
                <table className='table' id= 'opening'>
                    <tr>  
                      <th>Command input</th>
                      <th>Dance Move</th>
                    </tr>
                    <tr>  
                      <td>Left</td>
                      <td>Left arm up</td>
                    </tr>
                    <tr>  
                      <td>Right</td>
                      <td>Right arm up</td>
                    </tr>
                    <tr>  
                      <td>Jump</td>
                      <td>Performs a jumping jack</td>
                    </tr>
                    <tr>  
                      <td>Spin</td>
                      <td>Spins on the screen!</td>
                    </tr>
                </table> 
              </header>
            </div>
            <ParticlesBg type="cobweb" bg={true} /> 
          </>
  )};
}

export default App;
