import React from 'react';

const Robot = (c) => {
	var key = c['c'];
	// console.log(key);
	return (
			<div className={`${key} robots`}>
				<div className="android"> 
					<div className="head"> 
						<div className="eyes"> 
							<div className="left_eye"></div> 
							<div className="right_eye"></div>
						</div> 
					</div> 
					<div className="upper_body"> 
						<div className={`${key} left_arm`}></div> 
						<div className="torso"></div> 
						<div className={`${key} right_arm`}></div> 
					</div> 
					<div className="lower_body"> 
						<div className={`${key} left_leg`}></div> 
						<div className={`${key} right_leg`}></div> 
					</div> 
				</div>
			</div>
		)
}

export default Robot;